import React from "react";
import {connect} from "react-redux"
import {setPriceIsNet} from "eshop/modules/cart/actions/cart"
import {getPriceIsNet} from "eshop/modules/cart/selectors"
import {getMarketContext} from "eshop/modules/configurator/selectors/filters"

class MarketContextCheckboxView extends React.Component {


    isNet(event) {
        event.preventDefault();
        event.stopPropagation();
        this.props.setPriceIsNet(true);
    }

    isGross(event) {
        event.preventDefault();
        event.stopPropagation();
        this.props.setPriceIsNet(false);
    }

    render() {
        console.log("MarketContextCheckboxView>>>>>>>>>>>>>>>>>>", this.props.showNet);
        if (!this.props.isB2B) {
            return null;
        }

        return (
            <div className="opl-switch-btn u-right u-small-spacing-top-m">
                <input className="opl-switch-btn--left" type="radio" id="switch_left" name="switch" value="Opłaty netto"
                       checked={this.props.showNet} onChange={()=>{}}/>
                <label htmlFor="switch_left"><span className="opl-switch-btn--text opl-switch-btn--text-l"
                                               onClick={this.isNet.bind(this)}>
                <span className="u-vertical-middle">Opłaty netto</span></span>
                </label>
                <input className="opl-switch-btn--right" type="radio" id="switch_right" name="switch"
                       value="Opłaty brutto" checked={!this.props.showNet} onChange={()=>{}}/>
                <label htmlFor="switch_right"><span className="opl-switch-btn--text opl-switch-btn--text-r"
                                                onClick={this.isGross.bind(this)}>
                <span className="u-vertical-middle">Opłaty brutto</span></span>
                </label><span className="opl-switch-btn--bg"/>
            </div>
        );
    }
}

const mapStateToProps = state => ({
    showNet: getPriceIsNet(state),
    isB2B: getMarketContext(state) === "B2B",
});

const mapDispatchToProps = dispatch => ({
    setPriceIsNet: on => dispatch(setPriceIsNet(on)),
});

export default connect(mapStateToProps, mapDispatchToProps)(MarketContextCheckboxView);
